/* Estilos para la barra de navegación */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(255, 255, 255); /* Fondo transparente estilo vidrio */
  backdrop-filter: blur(10px); /* Efecto de vidrio borroso */
  color: #333; /* Color del texto para que sea legible */
  padding: 10px 20px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2); /* Sombra sutil */
  backdrop-filter: blur(5px);
}

.logo a {
  color: black;
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
}

.menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu li {
  margin-right: 20px;
}

.menu li:last-child {
  margin-right: 0;
}

.menu a {
  color: #333; /* Color del texto para que sea legible */
  text-decoration: none;
}

.menu a {
  transition: 0.3s ease-in-out; /* Ajusta la duración de la transición según tus preferencias */
}

.cart {
  display: flex;
  align-items: center;
}

.cart a {
  display: flex;
  align-items: center;
  color: white;
  text-decoration: none;
}

/* Estilo para la animación del contador */
.contador-animado {
  animation-name: contadorScale;
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
  transform-origin: center;
}

/* Definición de la animación */
@keyframes contadorScale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.cart img {
  width: 30px;
  height: 30px;
  margin-right: 5px;
}

.cart-count {
  background-color: #8DB1AB;
  color: white;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
  font-size: 14px;
}

/* Estilos para hacer la barra de navegación responsive (sin cambios) */
@media (max-width: 768px) {
  .menu {
    display: none;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    backdrop-filter: blur(10px);
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    padding: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  }
  .menu.active {
    display: flex;
  }
  .menu li {
    margin: 10px 0;
  }
  .menu a {
    color: #333; /* Color del texto para que sea legible */
    text-decoration: none;
  }
  .menu-toggle {
    display: block;
    cursor: pointer;
  }
  .bar {
    width: 30px;
    height: 3px;
    background-color: black;
    margin: 6px 0;
    transition: 0.4s;
  }
  .menu-open .bar:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px);
  }
  .menu-open .bar:nth-child(2) {
    opacity: 0;
  }
  .menu-open .bar:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px);
  }
}
/* Estilos para el footer */
