/* Estilo predeterminado para ocultar el checkbox nativo */
.custom-checkbox {
  display: none;
}

/* Estilo del contenedor del checkbox */
.custom-checkbox-label {
  position: relative;
  padding-left: 25px; /* Espacio a la izquierda del checkbox */
  cursor: pointer;
  font-size: 14px; /* Tamaño de fuente más pequeño */
  margin: 10px;
}

/* Estilo de la marca de verificación (el tick) */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px; /* Altura del checkbox */
  width: 20px; /* Ancho del checkbox */
  background-color: #fff; /* Color de fondo blanco inicial */
  border: 1px solid #333; /* Borde oscuro */
  border-radius: 4px; /* Borde redondeado */
}

/* Cambia el color de fondo y el borde cuando el checkbox está marcado */
.custom-checkbox:checked + .checkmark {
  background-color: #333; /* Cambia a fondo oscuro cuando está marcado */
  border: 1px solid #fff; /* Borde blanco cuando está marcado */
}

/* Estilo del tick dentro del checkbox */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Muestra el tick cuando el checkbox está marcado */
.custom-checkbox:checked + .checkmark:after {
  left: 6px; /* Posición horizontal del tick */
  top: 3px; /* Posición vertical del tick */
  width: 5px; /* Ancho del tick */
  height: 10px; /* Altura del tick */
  border: solid #fff; /* Borde blanco para el tick */
  border-width: 0 2px 2px 0; /* Estilo de la línea del tick */
  transform: rotate(45deg); /* Gira el tick 45 grados */
  content: "";
  display: block;
}

